.container{
  display: flex;
  justify-content: center;
}
.calculator{
  box-shadow: 0 0 10px black;
  width: 400px;
  height: 400px;
  margin-top: 100px;
}
.display{
  border-bottom: 1px solid black;
  padding: 3% 10px;
}
.glass{
  border: 4px solid rgb(202, 196, 196);
  box-shadow: 0 0 10px rgb(192, 188, 188);
  padding: 20px 10px;
  font-size: 24px;

}
.NumbersBox table{
  width: 100%;
  height: auto;
  padding: 10px;
}
.NumbersBox table td{
  padding: 10px;
  border: 1px solid #000000;
  border-radius: 50%;
  box-shadow:inset 0 0 10px black;
}
.NumbersBox table td:hover{
  background-color: rgb(124, 190, 190);
  cursor: pointer;
}